import { useState } from "react";
import axios from "axios";
import useDownloader from "react-use-downloader";

import "./App.css";

function App() {
  const { size, elapsed, percentage, download, cancel, error, isInProgress } =
    useDownloader();

  const fileUrl = "https://tinyurl.com/2ez9veks";
  const filename = "video.mp4";

  const [url, setUrl] = useState("");
  const [downloadFileUrl, setDownloadFileUrl] = useState(
    "https://tinyurl.com/2qcfrdy8"
  );

  function forceDownload(blob, filename) {
    var a = document.createElement("a");
    a.download = filename;
    a.href = blob;
    // For Firefox https://stackoverflow.com/a/32226068
    document.body.appendChild(a);
    a.click();
    a.remove();
  }

  // Current blob size limit is around 500MB for browsers
  function downloadResource(url, filename) {
    if (!filename) filename = url.split("\\").pop().split("/").pop();
    fetch(url, {
      // https://tinyurl.com/2jmcaem3
      headers: new Headers({
        // Origin: window.location.origin,
        Origin: 'https://dl218.y2mate.com/'
      }),
      mode: "cors",
    })
      .then((response) => response.blob())
      .then((blob) => {
        let blobUrl = window.URL.createObjectURL(blob);
        forceDownload(blobUrl, filename);
      })
      .catch((e) => console.error(e));
  }

  const Download = () => {
    if (url?.length > 4) {
      if (url.substr(0, 4) === "http") {
        downloadResource(url);
        // downloadResource('?')
        // fetch(url, {
        //   mode: "no-cors",
        // }).then((response) => {
        //   response.blob().then((blob) => {
        //     let url = window.URL.createObjectURL(blob);
        //     let a = document.createElement("a");
        //     a.href = url;
        //     a.download = "video.mp4";
        //     a.click();
        //   });
        //   //window.location.href = response.url;
        // });
      } else {
        alert("url field must be of http protocol");
      }
    } else {
      alert("url field empty or invalid");
    }
  };

  return (
    <div className="App">
      <h1>hello</h1>
      <input
        onChange={(e) => {
          setUrl(e.target.value);
        }}
        type="text"
        name=""
        id=""
      />
         {/*onClick={Download}*/}
      {/*onClick={() => download(fileUrl, filename)}*/}
      <button onClick={Download} >Download</button>
      <br />
      <a href={downloadFileUrl} onClick={() => axios.get(downloadFileUrl)}>
        Download File
      </a>
    </div>
  );
}

export default App;
